"use client";

import React, { useState, useEffect, useRef } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Skeleton } from "@/components/ui/skeleton";
import { useToast } from "@/components/ui/toast";
import { Send, Bot, User, Sparkles, TrendingUp, Package, ShoppingCart, BarChart3 } from "lucide-react";

interface Message {
  id: string;
  content: string;
  sender: "user" | "ai";
  timestamp: Date;
}

const suggestedQuestions = [
  "What are my top-performing products this month?",
  "Show me inventory levels for critical items",
  "Analyze my customer satisfaction trends",
  "Which customers have pending orders?",
  "Generate a sales performance report",
  "What's my order fulfillment rate?",
  "Show me overdue invoices",
  "Predict next month's demand"
];

export default function SupplierAIChat() {
  const { show } = useToast();
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputMessage, setInputMessage] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  useEffect(() => {
    // Welcome message
    const welcomeMessage: Message = {
      id: "welcome",
      content: "Hello! I'm your AI business assistant. I can help you analyze your supplier performance, track orders, manage inventory, and provide business insights. What would you like to know?",
      sender: "ai",
      timestamp: new Date()
    };
    setMessages([welcomeMessage]);
  }, []);

  const sendMessage = async (content: string) => {
    if (!content.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      content: content.trim(),
      sender: "user",
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage("");
    setIsLoading(true);

    try {
      // Simulate AI response
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      const aiResponse = generateAIResponse(content);
      const aiMessage: Message = {
        id: (Date.now() + 1).toString(),
        content: aiResponse,
        sender: "ai",
        timestamp: new Date()
      };

      setMessages(prev => [...prev, aiMessage]);
    } catch (err: any) {
      show({
        variant: "destructive",
        title: "Failed to send message",
        description: err?.message || "Unable to get AI response",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const generateAIResponse = (userMessage: string): string => {
    const lowerMessage = userMessage.toLowerCase();
    
    if (lowerMessage.includes("top") && lowerMessage.includes("product")) {
      return "ðŸ“Š **Top Performing Products This Month:**\n\n1. **Paracetamol 500mg** - 2,847 units sold ($28,470 revenue)\n2. **Ibuprofen 200mg** - 1,923 units sold ($19,230 revenue)\n3. **Aspirin 100mg** - 1,654 units sold ($16,540 revenue)\n4. **Amoxicillin 250mg** - 1,432 units sold ($21,480 revenue)\n5. **Omeprazole 20mg** - 1,287 units sold ($25,740 revenue)\n\n**Key Insights:**\nâ€¢ Pain relief medications show highest volume\nâ€¢ Antibiotics have better profit margins\nâ€¢ Consider increasing stock for top 3 products";
    }
    
    if (lowerMessage.includes("inventory") || lowerMessage.includes("stock")) {
      return "ðŸ“¦ **Current Inventory Status:**\n\n**Critical Stock Levels:**\nâ€¢ Ibuprofen 200mg: 45 units (Reorder: 100)\nâ€¢ Aspirin 100mg: 23 units (Reorder: 50)\nâ€¢ Vitamin D3: 12 units (Reorder: 75)\n\n**Well Stocked:**\nâ€¢ Paracetamol 500mg: 450 units\nâ€¢ Amoxicillin 250mg: 320 units\nâ€¢ Omeprazole 20mg: 280 units\n\n**Recommendations:**\nâ€¢ Place urgent orders for critical items\nâ€¢ Consider bulk purchasing for fast-moving products\nâ€¢ Review reorder levels for seasonal items";
    }
    
    if (lowerMessage.includes("customer") && lowerMessage.includes("satisfaction")) {
      return "â­ **Customer Satisfaction Analysis:**\n\n**Overall Rating:** 94.5% (Excellent)\n\n**Breakdown by Category:**\nâ€¢ Product Quality: 96.2%\nâ€¢ Delivery Time: 93.8%\nâ€¢ Customer Service: 95.1%\nâ€¢ Pricing: 92.4%\n\n**Recent Feedback Highlights:**\nâœ… \"Excellent product quality and fast delivery\"\nâœ… \"Reliable supplier with consistent stock\"\nâš ï¸ \"Pricing could be more competitive\"\n\n**Action Items:**\nâ€¢ Maintain current quality standards\nâ€¢ Review pricing strategy for key products\nâ€¢ Continue focus on delivery excellence";
    }
    
    if (lowerMessage.includes("pending") && lowerMessage.includes("order")) {
      return "ðŸ“‹ **Pending Orders Summary:**\n\n**Total Pending:** 23 orders ($67,450 value)\n\n**By Priority:**\nðŸ”´ **Urgent (5 orders):**\nâ€¢ MediCorp - $15,750 (Due: Today)\nâ€¢ PharmaCare - $8,920 (Due: Tomorrow)\nâ€¢ HealthPlus - $6,340 (Due: Tomorrow)\n\nðŸŸ¡ **Standard (18 orders):**\nâ€¢ Various customers - $36,440 total\nâ€¢ Due dates: Next 3-7 days\n\n**Recommended Actions:**\n1. Process urgent orders immediately\n2. Prepare shipments for tomorrow's deliveries\n3. Contact customers for any potential delays";
    }
    
    if (lowerMessage.includes("report") || lowerMessage.includes("performance")) {
      return "ðŸ“ˆ **Sales Performance Report - Current Month:**\n\n**Revenue:** $125,750 (+15% vs last month)\n**Orders:** 1,847 (+8% vs last month)\n**Average Order Value:** $68.12 (+6% vs last month)\n\n**Top Categories:**\n1. Pain Relief: $45,230 (36%)\n2. Antibiotics: $32,150 (26%)\n3. Vitamins: $28,940 (23%)\n4. Cardiovascular: $19,430 (15%)\n\n**Performance Metrics:**\nâ€¢ Order Fulfillment Rate: 98.2%\nâ€¢ Customer Retention: 89.4%\nâ€¢ Inventory Turnover: 8.4x\nâ€¢ Profit Margin: 24.8%\n\n**Growth Opportunities:**\nâ€¢ Expand vitamin product line\nâ€¢ Target new pharmacy chains\nâ€¢ Optimize pricing for antibiotics";
    }
    
    return "I understand you're asking about your supplier business. I can help you with:\n\nðŸ” **Analytics & Reports**\nâ€¢ Sales performance analysis\nâ€¢ Product performance metrics\nâ€¢ Customer satisfaction insights\n\nðŸ“¦ **Inventory Management**\nâ€¢ Stock level monitoring\nâ€¢ Reorder recommendations\nâ€¢ Demand forecasting\n\nðŸ‘¥ **Customer Management**\nâ€¢ Order status tracking\nâ€¢ Customer performance analysis\nâ€¢ Relationship insights\n\nðŸ’° **Financial Insights**\nâ€¢ Revenue analysis\nâ€¢ Profit margin optimization\nâ€¢ Payment tracking\n\nPlease ask me something specific, or try one of the suggested questions below!";
  };

  const handleSuggestedQuestion = (question: string) => {
    sendMessage(question);
  };

  return (
    <div className="p-6 h-full flex flex-col">
      <div className="mb-6">
        <h2 className="text-2xl font-bold text-gray-900 mb-2 flex items-center">
          <Sparkles className="h-6 w-6 mr-2 text-blue-600" />
          AI Business Assistant
        </h2>
        <p className="text-gray-600">Get intelligent insights about your supplier business</p>
      </div>

      <div className="flex-1 flex gap-6">
        {/* Chat Area */}
        <div className="flex-1 flex flex-col">
          <Card className="flex-1 flex flex-col">
            <CardHeader className="border-b">
              <CardTitle className="flex items-center">
                <Bot className="h-5 w-5 mr-2 text-blue-600" />
                Chat with AI Assistant
              </CardTitle>
            </CardHeader>
            <CardContent className="flex-1 flex flex-col p-0">
              {/* Messages */}
              <div className="flex-1 overflow-y-auto p-4 space-y-4">
                {messages.map((message) => (
                  <div
                    key={message.id}
                    className={`flex ${message.sender === "user" ? "justify-end" : "justify-start"}`}
                  >
                    <div
                      className={`max-w-[80%] rounded-lg p-3 ${
                        message.sender === "user"
                          ? "bg-blue-600 text-white"
                          : "bg-gray-100 text-gray-900"
                      }`}
                    >
                      <div className="flex items-start space-x-2">
                        {message.sender === "ai" && (
                          <Bot className="h-4 w-4 mt-1 text-blue-600" />
                        )}
                        {message.sender === "user" && (
                          <User className="h-4 w-4 mt-1 text-white" />
                        )}
                        <div className="flex-1">
                          <div className="whitespace-pre-wrap text-sm">
                            {message.content}
                          </div>
                          <div className={`text-xs mt-1 ${
                            message.sender === "user" ? "text-blue-100" : "text-gray-500"
                          }`}>
                            {message.timestamp.toLocaleTimeString()}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
                {isLoading && (
                  <div className="flex justify-start">
                    <div className="bg-gray-100 rounded-lg p-3">
                      <div className="flex items-center space-x-2">
                        <Bot className="h-4 w-4 text-blue-600" />
                        <div className="flex space-x-1">
                          <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                          <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: "0.1s" }}></div>
                          <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
                <div ref={messagesEndRef} />
              </div>

              {/* Input */}
              <div className="border-t p-4">
                <div className="flex space-x-2">
                  <Input
                    value={inputMessage}
                    onChange={(e) => setInputMessage(e.target.value)}
                    placeholder="Ask me about your business performance, inventory, customers..."
                    className="text-black"
                    onKeyPress={(e) => e.key === "Enter" && sendMessage(inputMessage)}
                    disabled={isLoading}
                  />
                  <Button
                    onClick={() => sendMessage(inputMessage)}
                    disabled={isLoading || !inputMessage.trim()}
                    className="bg-blue-600 hover:bg-blue-700"
                  >
                    <Send className="h-4 w-4" />
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Suggested Questions */}
        <div className="w-80">
          <Card>
            <CardHeader>
              <CardTitle className="text-sm">Suggested Questions</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2">
              {suggestedQuestions.map((question, index) => (
                <Button
                  key={index}
                  variant="outline"
                  size="sm"
                  className="w-full text-left justify-start h-auto p-3 text-xs"
                  onClick={() => handleSuggestedQuestion(question)}
                  disabled={isLoading}
                >
                  {question}
                </Button>
              ))}
            </CardContent>
          </Card>

          <Card className="mt-4">
            <CardHeader>
              <CardTitle className="text-sm">Quick Actions</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2">
              <Button asChild variant="outline" size="sm" className="w-full">
                <a href="/dashboard/supplier/analytics">
                  <TrendingUp className="h-4 w-4 mr-2" />
                  View Analytics
                </a>
              </Button>
              <Button asChild variant="outline" size="sm" className="w-full">
                <a href="/dashboard/supplier/products">
                  <Package className="h-4 w-4 mr-2" />
                  Manage Products
                </a>
              </Button>
              <Button asChild variant="outline" size="sm" className="w-full">
                <a href="/dashboard/supplier/orders">
                  <ShoppingCart className="h-4 w-4 mr-2" />
                  View Orders
                </a>
              </Button>
              <Button asChild variant="outline" size="sm" className="w-full">
                <a href="/dashboard/supplier/inventory">
                  <BarChart3 className="h-4 w-4 mr-2" />
                  Check Inventory
                </a>
              </Button>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}